import { FaceScene } from "@/AR-Renderer/scenes/face_scene.js";
import { TargetScene } from "@/AR-Renderer/scenes/target_scene";
import { CreateCameraVideo, TakePhoto } from "@/AR-Renderer/camera_video";

class SceneLoader {
    constructor(unityContainer, videoContainer, vue) {
        this.unityContainer = unityContainer;
        this.videoContainer = videoContainer;
        this.mindARContainerId = vue.mindARContainerId;
        this.showLoadingIndicator = vue.ShowLoadingIndicator;
        this.showScanningIndicator = vue.ShowScanningIndicator;
        this.cameraVideo = null;
        this.currentScene = null;
        this.allScenes = [];

        window.SCENE_LOADER = this;
    }

    async LoadScene(sceneType, sceneIndex, controller, targetImg) {
        this.showLoadingIndicator(true);
        sceneIndex = Number(sceneIndex);

        var mindARContainer
        var scene;

        switch (sceneType) {
            case "face":
                this.videoContainer.style.transform = 'scale(-1, 1)';
                this.videoContainer.style.display = 'none';

                if (this.allScenes[sceneIndex]) {
                    return this.SwitchScene(this.allScenes[sceneIndex]);
                }

                mindARContainer = this.CreateMindArContainer();
                mindARContainer.style.transform = 'scale(-1, 1)';

                scene = new FaceScene(sceneIndex, controller);
                await scene.Initialize();

                break;
            case "image":
                this.videoContainer.style.transform = 'scale(1, 1)';
                this.videoContainer.style.display = 'none';

                if (this.allScenes[sceneIndex]) {
                    return this.SwitchScene(this.allScenes[sceneIndex]);
                }

                mindARContainer = this.CreateMindArContainer();
                mindARContainer.style.transform = 'scale(1, 1)';

                var targetsPath = `./Targets/${targetImg}.png`;

                scene = new TargetScene(sceneIndex, controller);
                await scene.Initialize(targetsPath);

                break;
            case "surface":
                this.videoContainer.style.transform = 'scale(1, 1)';
                this.videoContainer.style.display = 'block';

                if (this.allScenes[sceneIndex]) {
                    return this.SwitchScene(this.allScenes[sceneIndex]);
                }

                if (this.video == null) {
                    let video = await CreateCameraVideo('environment');
                    this.video = video;
                    this.videoContainer.appendChild(this.video);
                }

                if (this.currentScene !== null) {
                    await this.currentScene.stop();
                    this.currentScene.mindARContainer.remove();
                }

                this.showLoadingIndicator(false);
                return;
            default:
                throw new Error(`${scene} is not correct scene type!`);
        }

        await scene.CreateMindAr(mindARContainer);
        this.allScenes[scene.sceneIndex] = scene;
        this.SwitchScene(scene);
    }

    //Call from unity
    async ScanSurface(sceneIndex, controller) {
        sceneIndex = Number(sceneIndex);
        if (this.video !== null) {

            let mindARContainer = this.CreateMindArContainer();
            mindARContainer.style.transform = 'scale(1, 1)';

            this.showScanningIndicator(true);

            var targetsPath = TakePhoto(this.video);

            let scene = new TargetScene(sceneIndex, controller);
            await scene.Initialize(targetsPath);
            await scene.CreateMindAr(mindARContainer);

            this.videoContainer.style.display = 'block';

            this.showScanningIndicator(false);

            this.allScenes[scene.sceneIndex] = scene;
            this.SwitchScene(scene);
        }
    }

    async SwitchScene(scene) {
        if (this.currentScene !== null) {
            await this.currentScene.stop();
            this.currentScene.mindARContainer.remove();
        }

        let container = scene.mindARContainer;
        this.unityContainer.parentElement.appendChild(container);
        this.currentScene = scene;

        if (this.currentScene.start) {
            await this.currentScene.start();
        }

        this.showLoadingIndicator(false);
    }

    CreateMindArContainer() {
        let mindARContainer = document.createElement("div");
        mindARContainer.setAttribute("id", this.mindARContainerId);
        mindARContainer.setAttribute("class", "fixed");
        return mindARContainer;
    }
}



export { SceneLoader };