<template>
    <div id="app" ref="app">
        <button @click="SetFullscreen(true)" ref="fullscreenBtn" id="fullscreenBtn">Fullscreen</button>
        <div v-show="true"
             id="container"
             class="fixed">
            <canvas id="unity-canvas"
                    class="fixed"
                    ref="uc">
            </canvas>
            <div id="videoContainer"
                 class="fixed"
                 ref="vc">
            </div>
        </div>

        <div v-if="ShowLoading"
             id="loaderMain"
             class="fixed">
            <div id="loadingIndicator">
                <div class="loader"></div>
            </div>
        </div>

        <div v-if="ShowScanning"
             id="scanningMain"
             class="fixed">
            <div id="loadingIndicator">
                <div class="loader"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import * as AR_Renderer from "@/AR-Renderer/ar_renderer";
    import { SceneLoader } from "@/AR-Renderer/scene_loader"

    export default {
        name: 'App',
        data() {
            return {
                isFullscreen: false,
                sceneLoader: null,
                ShowLoading: false,
                ShowScanning: false,
                mindARContainerId: "mindArContainer",
            };
        },
        created() {
            addEventListener('beforeunload', () => {
                fetch("/quit").catch((err) => {
                    throw err;
                });
            });
        },
        mounted() {
            this.Initialization();
        },
        methods: {
            async Initialization() {
                let unityContainer = this.$refs.uc;
                let videoContainer = this.$refs.vc;

                this.sceneLoader = new SceneLoader(unityContainer, videoContainer, this);

                AR_Renderer.Init(unityContainer, () => {
                    setTimeout(() => {
                        if (this.$refs.fullscreenBtn) {
                            this.$refs.fullscreenBtn.remove();
                        }
                    }, 10500);
                });
            },

            SetFullscreen(state) {
                this.$refs.fullscreenBtn.remove();
                if (state) {
                    this.$refs.app.requestFullscreen()
                        .then(() => {
                            this.isFullscreen = true;
                        })
                        .catch((err) => {
                            console.error(err);
                        });
                } else {
                    document.exitFullscreen()
                        .then(() => {
                            this.isFullscreen = false;
                        })
                        .catch((err) => {
                            console.error(err);
                        })
                }
            },

            ShowLoadingIndicator(state) {
                this.ShowLoading = state;
            },
            ShowScanningIndicator(state) {
                this.ShowScanning = state;
            },
        }
    }
</script>

<style>
    @import '../css/styles.css';

    .fixed {
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right:0;
        bottom:0;
    }

    #fullscreenBtn {
        position: fixed;
        top: 0;
        left: 50%;
        right: 50%;
        width: 200px;
        transform:translate(-50%, 0);
        height: 40px;
        z-index: 2;
        background-color: lightgray;
        color: grey;
        opacity: 0;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        border: none;
        animation: fade 2s linear infinite;
    }

    @keyframes fade {
        0% {
            opacity: 0.4;
        }
        50% {
            opacity: 0.2;
        }
        100% {
            opacity: 0.4;
        }
    }

    #unity-canvas {
        z-index: 1;
    }

    #container {
        z-index: 0;
    }

    #videoContainer {
        z-index: 0;
    }

    #mindArContainer {
        z-index: 0;
    }

    #loaderMain {
        z-index: 1;
        background-color: #3b3b3b
    }

    #scanningMain {
        z-index: 1;
    }

    #loadingIndicator {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .loader {
        border: 16px solid #f3f3f3; /* Light grey */
        border-top: 16px solid #3498db; /* Blue */
        border-radius: 50%;
        width: 120px;
        height: 120px;
        animation: spin 2s linear infinite;
    }

    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }
</style>
